.prices {
  color: white;
  padding-top: 15px;
  padding-bottom: 15px;
}

.prices-title {
  font-size: 30px;
  text-align: center;
  line-height: 1;
  margin-bottom: 15px;
}

.prices-list {
  list-style-type: none;
  margin-top: 15px;
  margin-bottom: 15px;
}

.prices-list li {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid white;
  font-size: 22px;
  margin-bottom: 2px;
}

span:nth-child(2) {
    padding-left: 20px;
}

